import React from 'react'
import LandingBaseLayout from '../LandingBaseLayout'
import { useFormik } from "formik";
import axios from 'axios';
import TextField from "../../common/TextField"
import TextArea from "../../common/TextArea"
import classnames from 'classnames';
import { ModalPopup } from '../../../actions/constant';


const yup = require('yup')
const Contact = () => {

    const INITIAL_VALUES = {
        name: "",
        email: "",
        message: "",


    }

    const validationSchema = yup.object({
        name: yup.string().required('Required field'),
        message: yup.string().required('Required field'),
        email: yup.string().email('Invalid email address').required('Required field'),

    })


    const formik = useFormik({
        initialValues: INITIAL_VALUES,
        validateOnChange: false,
        onSubmit: async (values) => {
            try {
                let result = await axios.post("/api/setting/contact", values)
                if (result.data) {
                    ModalPopup.success(
                        'Message Sent Successfully',
                        `Our Support Team Will Reach Out to You Shortly.`,
                        'OK',
                        () => {
                            formik.resetForm()
                        }
                    );

                }
            } catch (err) {
                ModalPopup.failure(
                    'Error',
                    `Error Occured Try Again`,
                    'OK',
                    () => {
                        window.location.reload()
                    }
                );
                console.log("errr", err)
            }

        },
        validationSchema
    })

    return (
        <LandingBaseLayout>
            <section className="hero-4 pt-lg-6 pb-sm-9 pb-6 pt-9">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-7 text-center">
                            <h1 className="hero-title mb-0">Contact Us</h1>
                            <p className="mb-4 fs-17 text-muted">
                                Please fill out the following form and we will get back to you shortly
                            </p>
                        </div>
                    </div>
                </div>
                <div className="shape bottom">
                    <svg
                        width="1440px"
                        height="40px"
                        viewBox="0 0 1440 40"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        xmlnsXlink="http://www.w3.org/1999/xlink"
                    >
                        <g
                            id="shape-b"
                            stroke="none"
                            strokeWidth={1}
                            fill="none"
                            fillRule="evenodd"
                        >
                            <g id="curve" fill="#fff">
                                <path
                                    d="M0,30.013 C239.659,10.004 479.143,0 718.453,0 C957.763,0 1198.28,10.004 1440,30.013 L1440,40 L0,40 L0,30.013 Z"
                                    id="Path"
                                />
                            </g>
                        </g>
                    </svg>
                </div>
            </section>
            <section className="section pb-lg-7 py-4 position-relative">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-6">
                            <div className="card shadow-none">
                                <div className="card-body p-xl-5 p-0">
                                    <h2 className="mb-2 mt-0 fw-medium">Let's Talk Further</h2>
                                    <p className="mb-5">
                                        Please fill out the following form and we will get back to you
                                        shortly
                                    </p>
                                    <form onSubmit={formik.handleSubmit}>
                                        <div className="row">
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <TextField
                                                        label="Name"
                                                        type="text"
                                                        id="name"
                                                        name="name"
                                                        onChange={formik.handleChange}
                                                        value={formik.values.name}
                                                        error={formik.errors.name}

                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <TextField
                                                        label="Email"
                                                        type="email"
                                                        id="email"
                                                        name="email"
                                                        onChange={formik.handleChange}
                                                        value={formik.values.email}
                                                        error={formik.errors.email}

                                                    />

                                                </div>
                                            </div>
                                            <div className="col-lg-12">

                                                <div className="mb-3">
                                                    <label
                                                        htmlFor="exampleFormControlTextarea1"
                                                        className="form-label"
                                                    >
                                                        Message <span className="text-danger">*</span>
                                                    </label>
                                                    <textarea
                                                        className={classnames('form-control', {
                                                            'is-invalid': formik.errors.message
                                                        })}
                                                        id="message"
                                                        name="message"
                                                        rows={4}
                                                        placeholder="Type Your Message..."
                                                        onChange={formik.handleChange}
                                                        value={formik.values.message}

                                                    />
                                                    {formik.errors.message && (<div className="invalid-feedback text-left text-danger">{formik.errors.message}</div>)}
                                                </div>
                                                <button type="submit" className="btn btn-primary">
                                                    Send
                                                    <span className="icon icon-xs text-white">
                                                        {/*?xml version="1.0" encoding="UTF-8"?*/}
                                                        <svg
                                                            width="24px"
                                                            height="24px"
                                                            viewBox="0 0 24 24"
                                                            version="1.1"
                                                            xmlns="http://www.w3.org/2000/svg"
                                                            xmlnsXlink="http://www.w3.org/1999/xlink"
                                                        >
                                                            {/* Generator: Sketch 52.2 (67145) - http://www.bohemiancoding.com/sketch */}
                                                            <title>Stockholm-icons / Communication / Send</title>
                                                            <desc>Created with Sketch.</desc>
                                                            <g
                                                                id="Stockholm-icons-/-Communication-/-Send"
                                                                stroke="none"
                                                                strokeWidth={1}
                                                                fill="none"
                                                                fillRule="evenodd"
                                                            >
                                                                <rect id="bound" x={0} y={0} width={24} height={24} />
                                                                <path
                                                                    d="M3,13.5 L19,12 L3,10.5 L3,3.7732928 C3,3.70255344 3.01501031,3.63261921 3.04403925,3.56811047 C3.15735832,3.3162903 3.45336217,3.20401298 3.70518234,3.31733205 L21.9867539,11.5440392 C22.098181,11.5941815 22.1873901,11.6833905 22.2375323,11.7948177 C22.3508514,12.0466378 22.2385741,12.3426417 21.9867539,12.4559608 L3.70518234,20.6826679 C3.64067359,20.7116969 3.57073936,20.7267072 3.5,20.7267072 C3.22385763,20.7267072 3,20.5028496 3,20.2267072 L3,13.5 Z"
                                                                    id="Combined-Shape"
                                                                    fill="#335EEA"
                                                                />
                                                            </g>
                                                        </svg>
                                                    </span>
                                                </button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-5 offset-lg-1">
                            <img
                                src="./assets/images/contact-us.svg"
                                className="img-fluid"
                                alt=""
                            />
                        </div>
                    </div>

                </div>
            </section>

        </LandingBaseLayout>
    )
}

export default Contact
