import React from 'react'
import classnames from 'classnames';
import PropTypes from 'prop-types';
import {Link} from "react-router-dom"

const TextAreaProfile = ({
    name,
    placeholder,
    value,
    label,
    error,
    info,
    type,
    onChange,
    disabled,
    rows,
    forgotView,
    forgotLink
}) => {

  return (
    
   
   <div className="row mb-6">
   {/*begin::Label*/}
   <label className="col-lg-4 col-form-label required fw-bold fs-6">{label}</label>
   {/*end::Label*/}
   {/*begin::Col*/}
   <div className="col-lg-8">
     {/*begin::Row*/}
     <div className="row">
       {/*begin::Col*/}
       <div className="col-lg-12 fv-row">
          {/*begin::Input*/}
     <textarea type={type} 
        className={classnames('form-control form-control-lg form-control-solid mb-3 mb-lg-0"',{
        'is-invalid': error
        })}
        placeholder={placeholder} 
        value={value}
        onChange={onChange}
        name={name}
        rows={rows}
        disabled={disabled} >
        </textarea>
        {info && <small className="form-text text-muted">{info}</small>}
        {error && (<div className="invalid-feedback text-left text-danger">{error}</div>)}
     {/*end::Input*/}
      
       </div>
       {/*end::Col*/}
      
     </div>
     {/*end::Row*/}
   </div>
   {/*end::Col*/}
 </div>
 
  )
}

TextAreaProfile.propTypes ={
    name: PropTypes.string.isRequired,
    placeholder: PropTypes.string,
    value: PropTypes.string.isRequired,
    info: PropTypes.string,
    error: PropTypes.string,
    type: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    disabled: PropTypes.string,
    label: PropTypes.string,
    forgotView: PropTypes.string,
    forgotLink: PropTypes.string,
    rows:PropTypes.string.isRequired,
}

TextAreaProfile.defaultProps = {
    type: 'text',
    rows:"4"
}

export default TextAreaProfile;